<template>
  <div class="main-content" :style="{ height: innerHeight + 'px' }">
    <div class="container-fluid">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Main',
  data(){
    return {
      innerHeight: 0
    }
  },
  mounted(){
    this.innerHeight = window.innerHeight - 60
  }
}
</script>
<style scoped lang="scss">
  .main-content {
    flex: 1;
    min-width: 0;
    min-height: 0;
    overflow: auto;
    background: #f7f7f7 url(./images/body-bg.png);
    box-shadow: 0 0 10px #757575;
    .container-fluid {
      padding-top: 20px;
      padding-right: 20px;
      padding-bottom: 20px;
      padding-left: 20px;
    }
  }

</style>


